/**
 * Reset some basic elements
 */
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

.intro {

    animation: pulseTXT 8s infinite;
    animation-direction: alternate;
	  color: #e34a4a;
    font-size: 2rem;
    line-height: 1;
    margin-bottom: 4rem;
    @include media-query($on-laptop) {
	font-size: 3rem;
	line-height: 1.1;
    }
}

#particles-js {
  background: none;
  position: fixed;
  top:0;
  bottom:0;
  width: 100%;
  height: 100%;
  z-index:-2;
}

$pulsate: #d3d;

.intro > span {
}

/**
 * Basic styling
 */


 @keyframes pulseBG {
   0% {
     background-color: rgb(15, 160, 255);
   }
   100% {
     background-color: #faff7e;
   }
 }

 @keyframes pulseTXT {
   0% {
     color: #faff7e;
   }

   50% {
    color: #0a1bff;
   }

   100% {
     color: rgb(15, 160, 255);
   }
 }

body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-color;
    //background: transparent;

    animation: pulseBG 10s infinite;
    animation-direction: alternate;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-ngs: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
}



/**
 * Set `margin-bottom` to maintain vertical rhythm
 */
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}



/**
 * Images
 */
img {
    max-width: 100%;
    vertical-align: middle;
}



/**
 * Figures
 */
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}



/**
 * Lists
 */
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
	margin-bottom: 0;
    }
}

dt {
    font-size: 1.2rem;
    font-weight: 600;
}

dd {
    margin-bottom: 1rem;
}


/**
 * Headings
 */
h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
}



/**
 * Links
 */
a {

      animation: pulseTXT 8s infinite;
      animation-direction: alternate;

    color: $brand-color;
    text-decoration: none;

    &:visited {
	color: darken($brand-color, 15%);
    }

    &:hover {
	color: $text-color;
	text-decoration: underline;
    }
}



/**
 * Blockquotes
 */
blockquote {
    color: $grey-color;
    border-left: 4px solid $grey-color-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: -1px;
    font-style: italic;

    > :last-child {
	margin-bottom: 0;
    }
}



/**
 * Code formatting
 */
pre,
code {
    font-size: 15px;
    border: 1px solid $grey-color-light;
    border-radius: 3px;
    background-color: #eef;
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
	border: 0;
	padding-right: 0;
	padding-left: 0;
    }
}



/**
 * Wrapper
 */
.wrapper {
    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit} * 2));
    max-width:         calc(#{$content-width} - (#{$spacing-unit} * 2));
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
	//    max-width: -webkit-calc(#{$content-width} - (#{$spacing-unit}));
	//    max-width:         calc(#{$content-width} - (#{$spacing-unit}));
	padding-right: $spacing-unit / 2;
	padding-left: $spacing-unit / 2;
    }
}



/**
 * Clearfix
 */
%clearfix {

    &:after {
	content: "";
	display: table;
	clear: both;
    }
}



/**
 * Icons
 */
.icon {

    > svg {
	display: inline-block;
	width: 16px;
	height: 16px;
	vertical-align: middle;

	path {
	    fill: $grey-color;
	}
    }
}
